.toolbar-section {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.toolbar-section__input {
  outline: 0;
  color: #313131;
  padding: 0.67857143em 1em;
  border: 2px solid rgba(34, 36, 38, 0.15);
  border-radius: 6px;
}

.toolbar-section__button {
  cursor: pointer;
  outline: 0;
  border-radius: 50%;
  padding: 5px 11px;
  font-weight: bold;
  font-size: 20px;
}

.toolbar-section__button:disabled {
  cursor: not-allowed;
}
