.item-section {
  border: 1px solid #e8e8e8;
  border-radius: 6px;
  box-shadow: 0 1px 3px 0 #d4d4d5, 0 0 0 1px #d4d4d5;
  margin: 10px;
  line-height: 2;
  display: flex;
  align-items: center;
}

.item-section__check {
  margin-right: 20px;
  margin-left: 5px;
  width: 15px;
  height: 15px;
}

.item-section__content {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  overflow-wrap: break-word;
}
